.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content {
  width: calc(100% - 4rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  gap: 2rem;
}

@media only screen and (min-width : 700px) {
  .content {
    width: calc(700px - 4rem);
  }
}